import React from 'react';
import { CustomizedBiTagPluginType } from './type';

export default ({ renderContent }: CustomizedBiTagPluginType) => {
  return (props, controller, next) => {
    if (renderContent) {
      return (
        <>
          {next()}
          {renderContent(props, controller, next)}
        </>
      );
    }
    return (
      <>
        {next()}
      </>
    );
  };
};
